<template>
	<view style="margin-bottom: 20px;">
		<view class="wrapper">
			<!-- 用户信息 -->
			<view class="userInfo">
				<!-- 用户头像 -->
				<view class="userImg" style="background-color: #DBA503;">
					<image :src="cardInfo.userImg" mode="aspectFill"></image>
				</view>
				<!-- 用户名 -->
				<view class="userName">
					{{cardInfo.userName}}
				</view>
			</view>
			<!-- 文章显示 -->
			<view class="contentText" :style="userColor">
				{{cardInfo.contentText}}
			</view>

		</view>
		<view class="infoBox"v-if="cardInfo.isAnswered" v-show="cardInfo.isAnswered" @click="showPage">

		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {		
			}
		},
		props: {
			cardInfo: {
				
			}
		},
		mounted() {
			console.log(this.cardInfo);

		},
		methods:{
			showPage(){
				this.isShow = true;
			}
		}
	}
</script>

<style>
	.wrapper {
		margin: 0 auto;
		width: 90%;
		background: #444444;
		border-radius: 15px 15px 0px 0px;
	}

	/* 设置用户信息一栏的样式 */
	.userInfo {
		display: flex;
		flex-direction: row;
	}

	/* 设置用户头像的样式 */
	.userInfo>.userImg {
		margin: 15px;
		width: 15px;
		height: 15px;
		border-radius: 50%;

	}

	/* 设置用户名字 */
	.userInfo>.userName {
		font-size: 11px;
		height: 20px;
		line-height: 28px;
		margin-top: 8px;
		margin-bottom: 10px;
		/* 设置用户名文字样式 */
		width: 285px;
		height: 21px;
		font-weight: bold;
		color: #888888;
		line-height: 34px;

	}

	/* 文字内容区 */
	.contentText {
		margin: 0 auto;
		width: 90%;
		/* 设置卡片显示的文字样式 */
		font-size: 12px;
		font-weight: bold;
		color: #E2E2E2;
		line-height: 38px;

	}

	.infoBox {
		width: 85%;
		height: 49px;
		margin: 0 auto;
		background: #585858;
		border-radius: 0px 0px 15px 15px;
	}
</style>
